/****************************************************** header */
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f2f2f2;
  padding: 10px 40px;
  min-height: 40px;

  .header__logo {
    color: #000;
    font-weight: 600;
    font-size: 18px;
  }
}

header a {
  margin: 0 10px;
  text-decoration: none;
  color: gray;

  &:focus,
  &:hover {
    color: #000;
  }
}

/****************************************************** footer */
footer {
  background: #f2f2f2;
  min-height: 40px;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  gap: 20px;

  a {
    margin: 0 10px;
    text-decoration: none;
    color: gray;

    &:focus,
    &:hover {
      color: #000;
    }
  }
}

.footer__theme-btn {
  cursor: pointer;
  width: 18px;
  height: 18px;
}

/****************************************************** post navigation */
.post__navigation {
  display: flex;
  gap: 12px;
  margin: 0 auto;
  max-width: 680px;
  font-size: 16px;
  color: gray;
  cursor: pointer;
  padding: 48px 20px 0 20px;
}

.post__navigation--active {
  color: black;
  font-weight: 600;
}

/****************************************************** post list, post detail */
.post__list,
.post__detail {
  min-height: 90vh;
  padding: 20px;
  text-align: center;
  max-width: 680px;
  margin: 0 auto;
  text-align: left;
  line-height: 24px;
}

.post__box {
  padding-top: 20px;
  border-top: 1px solid #f2f2f2;
}

.post__profile-box {
  display: flex;
  gap: 8px;
  font-size: 14px;
  align-items: center;
}

.post__profile {
  width: 36px;
  height: 36px;
  background: #f2f2f2;
  border-radius: 50%;
}

.post__date,
.post__author-name {
  color: gray;
}

.post__title {
  font-size: 20px;
  font-weight: 600;
  margin: 14px 0;
}

.post__text {
  color: gray;
  font-size: 16px;
}

.post__list a {
  text-decoration: none;
  color: #000;
}

.post__utils-box {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-direction: row-reverse;
  font-size: 14px;
  color: gray;
  padding-bottom: 20px;
}

.post__delete {


  cursor: pointer;




}

.post__delete,
.post__edit {
  cursor: pointer;
}

.post__delete:focus,
.post__delete:hover {
  color: #000;
}

.post__edit:focus,
.post__edit:hover,
.post__edit a:focus,
.post__edit a:hover {
  color: #000;
}

.post__no-post {
  padding: 24px;
  text-align: center;
  color: gray;
  border: 1px solid #f2f2f2;
  border-radius: 20px;
}

/****************************************************** post detail */

.post__detail {
  .post__title {
    font-size: 36px;
    line-height: 40px;
  }

  .post__utils-box {
    padding: 10px 0;
    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
    flex-direction: row;
  }

  .post__profile-box {
    padding: 10px 0;
  }

  .post__text {
    padding: 10px 0;
  }
}

.post__edit a {
  text-decoration: none;
  color: gray;
}

.post__text--pre-wrap {
  word-spacing: pre-wrap;
}

.post__category {
  border: 1px solid #f2f2f2;
  color: gray;
  padding: 0 4px;
  border-radius: 10px;
  font-size: 12px;
}

/****************************************************** profile */
.profile__box {
  display: flex;
  gap: 18px;
  align-items: center;
  font-size: 18px;
  margin: 0 auto;
  max-width: 680px;
  text-align: left;
  line-height: 24px;
  justify-content: space-between;
  padding: 20px;
}

.flex__box-lg {
  display: flex;
  gap: 18px;
  align-items: center;
}

.profile__image {
  width: 72px;
  height: 72px;
  background: #f2f2f2;
  border-radius: 50%;
}

.profile__name {
  font-size: 16px;
  padding-top: 4px;
}

.profile__email {
  font-weight: 500;
}

.profile__logout {
  color: gray;
  font-size: 14px;
  cursor: pointer;
  text-decoration: underline;
}

.profile__logout:hover,
.profile__logoutL:focus {
  color: #000;
}

/****************************************************** form */
.form {
  margin: 0 auto;
  max-width: 680px;
  padding: 20px;
  margin-top: 20px;
}

.form input {
  height: 20px;
  padding: 10px 10px;
  font-size: 16px;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
  width: 96%;
  max-width: 680px;
}

.form textarea {
  min-height: 400px;
  line-height: 1.5;
  padding: 10px 10px;
  font-size: 16px;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
  width: 96%;
  max-width: 680px;
}

.form {
  .form__block {
    margin-top: 20px;
    width: 100%;
  }

  label {
    display: block;
    font-weight: 500;
    margin-bottom: 10px;
    margin-top: 20px;
  }

  .form__btn--submit {
    width: 100%;
    height: 48px;
    padding: 10px 10px;
    float: right;
    cursor: pointer;
    margin: 0 auto;
    background: #2563eb;
    font-size: 16px;
    color: #fff;
  }

  .form__btn--submit {

    &:hover,
    &:focus {
      color: #1945a4;
    }
  }
}

/****************************************************** login form */
.form--lg {
  min-height: 70vh;
  margin-top: 10vh;
}

.form__title {
  text-align: center;
  margin-bottom: 4px;
}

.form__link {
  margin-left: 10px;
  text-decoration: none;
  color: gray;
}

.form__link:hover,
.form__link:focus {
  color: #000;
}

.form__error {
  color: red;
}

.form select {
  border: 1px solid lightgray;
  max-width: 680px;
  height: 40px;
  padding: 0 20px;
  border-radius: 0.3rem;
}

/****************************************************** loader */

.loader {
  width: 48px;
  height: 48px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  bottom: 5px solid #2563eb;
  border-radius: 50%;
  z-index: 9999;
  animation: rotation 1s linear infinite;
}

/****************************************************** comment */
.comments {
  width: 100%;
}

.comments__form label {
  font-weight: 600;
  display: block;
  margin: 20px 0 10px;
}

.comments__form textarea {
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
  display: block;
  width: 100%;
  max-width: 680px;
  box-sizing: border-box;
}

.comments__form .form__block {
  margin-top: 10px;
  width: 100%;
  height: 100%;
}

.comments__form .form__btn-submit {
  width: 100px;
  height: 36px;
  cursor: pointer;
  font-weight: 500;
  background: #2563eb;
  color: #fff;
  border: none;
  border-radius: 5px;
}

.form__block-reverse {
  display: flex;
  flex-direction: row-reverse;
}

.form__block-reverse .form__btn-submit:hover,
.form__block-reverse .form__btn-submit:focus {
  background: #1945a4;
}

.comments__list {
  margin: 40px 0 100px 0;
}

.comment_box {
  padding: 12px 0;
  border-bottom: 1px solid #f2f2f2;
}

.comment__profile-box {
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 13px;
}

.comment__email {
  font-weight: 500;
}

.comment__date {
  color: gray;
}

.comment__delete {
  color: gray;
  font-size: 12px;
  cursor: pointer;
  text-decoration: underline;
}

.comment__delete:hover,
.comment__delete:focus {
  color: #000
}

.comment__text {
  font-size: 14px;
  padding-top: 4px;
}

/****************************************************** dark mode */
.white {
  transition: all 0.25s linear;
  background: #fff;
}

.dark {
  transition: all 0.25s linear;
  background: #1e2937;
  min-height: 100vh;
}

.dark {

  a,
  .form label,
  .post__delete,
  .profile__logout,
  .form__title,
  .post__navigation--active,
  .post__title,
  .post__author-name,
  .footer__theme-btn,
  .profile__name,
  .comments__list {
    color: #fff;
  }

  a:hover,
  a:focus,
  .post__text,
  .post__text--pre-wrap,
  .profile__email,
  .post__date,
  .form__block,
  .comment__date,
  .comment__delete {
    color: lightgray;
  }

  .comment__delete:hover,
  .comment__delete:focus {
    color: #fff
  }

  .form__error {
    color: #ff6a71;
  }

  .dark footer,
  header {
    background: #111827;
  }
}